

.set_quiz{
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  width: 50%;
  height: 400px;
  margin: auto;
}
.heading{
   text-align: center;
}
.form-container {
    display: flex;
    justify-content: center;
    margin-top: 50px;
  }
  
  form {
    width: 400px;
    margin: 0 auto;
  }
  
  input,
  select {
    width: 100%;
    box-sizing: border-box;
    padding: 5px;
    margin-bottom: 10px;
  }
  
  .pink_b {
    background-color: rgb(228, 15, 114);
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
    box-sizing: border-box;
    padding: 5px;
    margin-bottom: 10px;
  }